import router from '@/router'

// 获取浏览器语言
export const getLanguage = () => {
  return (navigator.browserLanguage || navigator.language).toLowerCase();
};

/**
 * @desc 函数防抖
 * @param func 函数
 * @param wait 延迟执行毫秒数
 * @param immediate true 表立即执行，false 表非立即执行
 */
export const debounce = (func, wait, immediate) => {
  let timeout;
  return function() {
    let context = this;
    let args = arguments;

    if (timeout) clearTimeout(timeout);
    if (immediate) {
      let callNow = !timeout;
      timeout = setTimeout(() => {
        timeout = null;
      }, wait);
      if (callNow) func.apply(context, args);
    } else {
      timeout = setTimeout(() => {
        func.apply(context, args);
      }, wait);
    }
  };
};

/**
 * @desc 函数节流
 * @param func 函数
 * @param wait 延迟执行毫秒数
 * @param type 1 表时间戳版，2 表定时器版
 */
export const throttle = (func, wait, type) => {
  if (type === 1) {
    var previous = 0;
  } else if (type === 2) {
    var timeout;
  }
  return function() {
    let context = this;
    let args = arguments;
    if (type === 1) {
      let now = Date.now();

      if (now - previous > wait) {
        func.apply(context, args);
        previous = now;
      }
    } else if (type === 2) {
      if (!timeout) {
        timeout = setTimeout(() => {
          timeout = null;
          func.apply(context, args);
        }, wait);
      }
    }
  };
};


/** 
 * @param {String} path 跳转的路由路径
 * @param {Number} time 间隔的时间(单位：S)
 * @description 监测无操作情况下，指定时间内跳转到指定路由
*/
export const monitor = (path, time) => {
  let overTime = 0
  let timer = null
  fn()
  // 监听触摸结束后长时间无操作
  document.documentElement.addEventListener('touchend', () => {
    fn()
  })
  // 执行定时器，指定时间内不操作，跳转指定path
  function fn() {
    timer = setInterval(() => {
      overTime++
      if(overTime > time) {
        // 跳转到指定路径
        router.push(path)
        clearInterval(timer)
      }
    }, 1000)
  }
  // 触摸移动时重置
  document.documentElement.addEventListener('touchmove', () => {
    overTime = 0
    clearInterval(timer)
  })
  // 触摸开始时重置
  document.documentElement.addEventListener('touchstart', () => {
    clearInterval(timer)
    overTime = 0
  })
}