<template>
  <div class="song-list">
    <img class="song-title" :src="songTitle" alt="" />
    <div class="list-container">
      <div class="list-row" style="margin-left: 11vw">
        <div v-for="item in listData" :key="item.id" class="list-item" style="padding-top: 13vh">
          <SongCard
            :url="item.url"
            :name="item.name"
            @click="handlePlayer(item.s_name)"
          />
        </div>
      </div>
      <div class="back-row" style="margin-top: 4vh;">
        <img class="back-btn" :src="backImg" alt="" @click="handleBack" />
      </div>
    </div>
  </div>
</template>

<script>
import { SongCard } from "@/components";
import { monitor } from '@/tools/tools'

export default {
  components: {
    SongCard
  },
  data() {
    return {
      songTitle: require("@/assets/songList/song_title.png"),
      backImg: require("@/assets/songList/back_btn@2x.png"),
      listData1: [
        { id: 0, name: "陕北民歌", url: require("@/assets/songList/03_sbmg.png"),s_name:'03_sbmg'},
        { id: 1, name: "黄土地的呼吸", url: require("@/assets/songList/03_htddhx.png"),s_name:'03_htddhx'},
        { id: 2, name: "走西口的长路", url: require("@/assets/songList/03_zxkdcl.png"),s_name:'03_zxkdcl' },
      ],
      listData2: [
        { id: 0, name: "蓝花花的绝唱", url: require("@/assets/songList/03_lhhdjc.png"),s_name:'03_lhhdjc'},
        { id: 1, name: "东方红的号角", url: require("@/assets/songList/03_dfhdhj.png"),s_name:'03_dfhdhj' },
        { id: 2, name: "悠远的回声", url: require("@/assets/songList/03_yydhs.png"),s_name:'03_yydhs' },
      ],
      listData:{}
    };
  },

  mounted() {
    monitor('/tradition', 20)
    const { type } = this.$route.query;
    if (type === 'bys') {
      this.listData = this.listData1
    }else{
      this.listData = this.listData2
    }
  },
  methods: {
    // 返回
    handleBack() {
      this.$router.push("/tradition/ditty");
    },
    // 播放视频
    handlePlayer(name) {
      this.$router.push({
        path: "/player",
        query: {
          src: name
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
@import "../../../styles/variables.less";

.song-list {
  width: 100vw;
  height: 100vh;
  background: url("../../../assets/home/home_bg.png");
  background-size: cover;
  text-align: center;
  position: relative;
  padding: 0 5vw;
  box-sizing: border-box;
  .song-title {
    width: 23.3vw;
    display: block;
    padding: 4.8vh 0 3.6vh;
    margin: 0 auto;
  }
  .list-container {
    width: 90vw;
    height: 80vh;
    background: url("../../../assets/songList/song_bg.png") no-repeat;
    background-size: 100% 100%;
    box-sizing: border-box;
    padding: 10vh 4.8vw 6.8vh;
    .list-row {
      display: flex;
      flex-wrap: wrap;
      padding-bottom: 6.8vh;
      max-height: 47.5vh;
      overflow-y: auto;
      .list-item {
        padding-right: 3.5vw;
        padding-bottom: 4.8vh;
        &:nth-child(4n) {
          padding-right: 0;
        }
        &:nth-child(n+4) {
          padding-bottom: 0;
        }
      }
    }

    .list-row::-webkit-scrollbar{
      width: 4px;
      height: 1px;
    }
    .list-row::-webkit-scrollbar-thumb{
      /*滚动条里面小方块*/
      border-radius   : 10px;
      background: linear-gradient(180deg, #C74754 0%, #96222A 100%);
    }
    .list-row::-webkit-scrollbar-track {
      /*滚动条里面轨道*/
      box-shadow   : inset 0 0 5px rgba(0, 0, 0, 0.2);
      background: #772025;;
      border-radius: 10px;
    }
    .back-row {
      text-align: center;
      .back-btn {
        width: 7vw;
      }
    }
  }
}
</style>
